<template>
<div>
    <!-- 头部 -->
    <Head :agood='1' :bgood='bgood' :cgood='3'></Head>
      <div class="log-content">
          <!-- 账户信息 -->
          <div class="account">
              <div class="log-1">
                  <span>头像</span>
                  <span>
                      <svg data-v-0fc4ab7a="" class="privateImage-svg" v-if="image==null">
                          <use data-v-0fc4ab7a="" xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#avatar-default">
                            <svg viewBox="0 0 122 122" id="avatar-default" width="100%" height="100%"><path fill="#DCDCDC" fill-rule="evenodd" d="M61 121.5c33.413 0 60.5-27.087 60.5-60.5S94.413.5 61 .5.5 27.587.5 61s27.087 60.5 60.5 60.5zm12.526-45.806c-.019 3.316-.108 6.052.237 9.825 3.286 8.749 18.816 9.407 28.468 17.891-1.833 1.998-6.768 6.788-15 10.848-7.02 3.463-16.838 6.416-24.831 6.416-17.366 0-32.764-7.149-42.919-17.264 9.713-8.407 25.49-9.173 28.769-17.891.345-3.773.258-6.509.24-9.825l-.004-.002c-1.903-.985-5.438-7.268-6.01-12.571-1.492-.12-3.843-1.561-4.534-7.247-.37-3.053 1.107-4.77 2.004-5.31-5.046-19.212 1.507-33.16 20.749-34.406 5.753 0 10.18 1.52 11.909 4.523 15.35 2.702 11.756 22.658 9.328 29.882.899.54 2.376 2.258 2.004 5.31-.689 5.687-3.042 7.127-4.534 7.248-.575 5.305-3.25 10.82-5.873 12.57l-.003.003zM61 120.5C28.14 120.5 1.5 93.86 1.5 61S28.14 1.5 61 1.5s59.5 26.64 59.5 59.5-26.64 59.5-59.5 59.5z"></path></svg>
                        </use></svg>
                        <img :src="'//elm.cangdu.org/img/'+image" alt="" v-else />
                   </span>
                   <input type="file" @change="upload" ref="upload" class="input">
              </div>
              <div>
                  <span>
                      用户名
                  </span>
                  <span>
                      {{name}}<van-icon name="arrow"/>
                   </span> 

              </div>
              <div @click="goods">
                  <span>
                     收货地址
                  </span>
                  <span><van-icon name="arrow"/></span>
              </div>
          </div>
          <!-- 账号绑定 -->
          <div class="bind">
              账号绑定
          </div>
          <!-- 手机 -->
          <div class="phone">
              <span>手机</span>
              <span><van-icon name="arrow"/></span>
          </div>
          <!-- 安全设置 -->
          <div class="bind">
              安全设置
          </div>
          <!-- 密码 -->
          <div class="phone" @click="phone">
              <span>登录密码</span>
              <span>修改<van-icon name="arrow"/></span>
          </div>
          <!-- 退出 -->
          <div class="sing">
              <mt-button type="danger" @click="logout" class='button'>退出登录</mt-button>
          </div>
      </div>
    
    
    
</div>  
</template>
<script>
import axios from 'axios'
import { MessageBox } from 'mint-ui';
import Head from '@/components/head/Head'
export default {
    name:'logout',
    components:{Head},
    data(){
        return{
            bgood:'账户信息',
            mm:[],
            name:'',
            image:'',
            
        }
    },
    beforeMount(){
        this.image=this.$store.state.login.headPhoto
        // console.log(this.$store.state.login.headPhoto)
    },
    created(){
        //给name赋值，显示在用户名上
        this.name=JSON.parse(localStorage.getItem('vuex')).login.user
    },
    methods:{
        //点击退出登录
        logout(){
            // MessageBox.confirm('是否退出登录?').then(action => {
                 
            //     });
           this.mm=localStorage.removeItem('vuex')
                 this.$router.push('/my')
            // var arr=JSON.parse(localStorage.getItem('login'))
            // arr=[]
            // localStorage.setItem('login',JSON.stringify(arr))
           
        },
        //跳转到重置密码页面
        phone(){
            this.$router.push('/reset')
        },
        //跳转到收货页面
        goods(){
            this.$router.push('/goods')
        },
        //换头像
        upload(){
              // 上传文件
              var id = this.$store.state.login.id
              console.log(id)
              const userUploadAtt= (File,config)=>axios.post('https://elm.cangdu.org/eus/v1/users/'+id+'/avatar',File,config)
              let input =this.$refs.upload
              //new 一个对象
              let data = new FormData()
              //用FormData.append为表单添加建值对到表单中
              data.append('file',input.files[0])
              console.log(data)
              userUploadAtt(data,{headers:{'Content-Type': 'multipart/form-data'}}).then(res=>{
                  // console.log(res)
                 this.image = res.data.image_path
                 this.$store.commit('imag',this.image)
              })
      },
    }
}
</script>
<style lang='scss'>
.van-uploader__upload{
    width:1.6rem;
    height: 1.4rem;
}
.van-uploader__preview-image{
    width:1.6rem;
    height: 1.4rem;
}
.log-content{
    width: 100%;
    margin-top: .91rem;
    .account{
        width: 100%;
        height: 3.5rem;
        background: white;
        margin-top: 1.15rem;
        
        .log-1{
            position: relative;
            height: 1.5rem;
            border-top: 0.01rem solid rgb(163, 160, 160);
            font-size: .32rem;
            line-height: 1.5rem;
            display: flex;
            justify-content: space-between;
            span{
                &:nth-of-type(1){
                    font-weight: bold;
                    padding-left: .15rem;
                }    
                &:nth-of-type(2){
                    color: grey;
                    padding-right: .15rem;
                    .privateImage-svg{
                        width: 1.5rem;
                        height: 1.5rem;
                    }
                    img{
                        width: 1.5rem;
                        height: 1.5rem;
                        border-radius: 50%;
                    }
                }     
            }
            .input{
                width: 100%;
                height: 100%;
                position: absolute;
                opacity: 0
            }
        }
        div{
            
            &:nth-of-type(2){
                height: 1rem;
                border-bottom: 0.01rem solid rgb(163, 160, 160);
                border-top: 0.01rem solid rgb(163, 160, 160);
                font-size: .32rem;
                line-height: 1rem;
                display: flex;
                justify-content: space-between;
                span{
                    &:nth-of-type(1){
                        font-weight: bold;
                        padding-left: .15rem;
                    }
                    &:nth-of-type(2){
                    color: grey;
                    padding-right: .15rem;
                    }  
                }
            }
            &:nth-of-type(3){
                height: 1rem;
                font-size: .32rem;
                border-bottom: 0.01rem solid rgb(163, 160, 160);
                line-height: 1rem;
                display: flex;
                justify-content: space-between;
                span{
                    &:nth-of-type(1){
                        font-weight: bold;
                        padding-left: .15rem;
                    }
                    &:nth-of-type(2){
                    color: grey;
                    padding-right: .15rem;
                    }  
                }
            }
        }
    }
    .bind{
        width: 100%;
        height: .8rem;
        font-size: .34rem;
        line-height: .8rem;
        padding-left: .15rem;
        color: grey;
    }
    .phone{
        width: 100%;
        height: 1rem;
        background: white;
        border-bottom: 0.01rem solid rgb(163, 160, 160);
        border-top: 0.01rem solid rgb(163, 160, 160);
        font-size: .32rem;
        line-height: 1rem;display: flex;
       
       justify-content: space-between;
        span{
                &:nth-of-type(1){
                    font-weight: bold;
                    padding-left: .15rem;
                } 
                 &:nth-of-type(2){
                    color: grey;
                    padding-right: .15rem;
                    }     
            }
    }
    .sing{
        width: 95%;
        height: 2.8rem;
        display: flex;
        align-items: center;
        margin: 0 auto;
        .button{
            width: 100%;
            height: 33%;
            font-size: .36rem;
            background: red;
        }
    }
}

</style>


